A,CACA,cAAA,CACA,iBAAA,CACA,eAAA,CACA,kBAGA,CACA,wDAKA,kBAAA,CAHA,aAAA,CAEA,UAAA,CAHA,cAAA,CAEA,UAGA,CACA,iCACA,UAAA,CACA,mBAAA,CACA,cAAA,CACA,iBAAA,CACA,eAAA,CACA,kBACA,CACA,sCACA,eACA,CACA,mFAIA,+BAAA,CAFA,yBAAA,CADA,iBAAA,CAKA,aAAA,CACA,mBAAA,CACA,cAAA,CACA,iBAAA,CACA,eAAA,CACA,gBAAA,CARA,YAAA,CAEA,UAOA,CAIA,iDACA,+BACA,CACA,8CAKA,kBAAA,CAJA,WAAA,CAGA,kBAAA,CAGA,UAAA,CAMA,oBAAA,CALA,kBAAA,CACA,kBAAA,CACA,iBAAA,CACA,eAAA,CACA,kBAAA,CATA,kBAAA,CAWA,gBAAA,CAZA,YAAA,CAIA,iBASA","sourcesContent":["<template>\r\n    <div class=\"testi-popup-cont\">\r\n            <div class=\"testi-form-cont\">\r\n                <div class=\"testi-form-head\">\r\n                    <div>\r\n                        <span>Assign to a Member</span>\r\n                        <span></span>\r\n                    </div>\r\n                    <img style=\"cursor: pointer;\" @click=\"$emit('close')\" src=\"/assets/clarity_window-close-line.svg\" alt=\"\">\r\n                </div>\r\n                <p class=\"testi-form-que mt-3\">Your members <span>List:</span></p>\r\n                <form>\r\n                    <div class=\"mb-3\">\r\n                        <multiselect\r\n                                v-model=\"selectedEdit\"\r\n                                placeholder=\"Select Member\"\r\n                                :options=\"members\"\r\n                                :show-labels=\"false\"\r\n                                :allow-empty=\"false\"\r\n                                track-by=\"name\"\r\n                                label=\"name\"\r\n                                ><span slot=\"noResult\">No Member Exists.</span>\r\n\r\n                            </multiselect>\r\n                    </div>\r\n                    <div class=\"d-flex justify-end\"><button type=\"button\" @click=\"addVenue()\" :disabled=\"disableSubmit\">{{selectedForm.buttonText}}</button></div>                    \r\n                </form>\r\n            </div>\r\n        </div>\r\n</template>\r\n<script>\r\nimport Multiselect from \"vue-multiselect\";\r\n\r\n    export default {\r\n        props: ['members'],\r\n        components: {\r\n            Multiselect,\r\n        },\r\n        emits: ['close', 'venue', 'message', 'all'],\r\n        data() {\r\n            return {\r\n                selectedForm: {\r\n                    heading: 'Add Your Venue',\r\n                    buttonText: 'Submit',\r\n                },\r\n                venueDetails: {\r\n                    name: null,\r\n                    address: null,\r\n                },\r\n                disableSubmit: false,\r\n                errorsSubmit: null,\r\n                editBtn: 'Edit Existing Venues',\r\n                edit: false,\r\n                editId: null,\r\n                selectedEdit: null,\r\n            }\r\n        },\r\n        methods: {\r\n            addVenue(){\r\n                let _this = this;\r\n                _this.disableSubmit = true;\r\n                _this.errorsSubmit = null;\r\n                let formData = new FormData();\r\n                // let code = _this.invi?.location_id?.code;\r\n                for (var key in _this.venueDetails) {\r\n                    formData.append(key, _this.venueDetails[key]);\r\n                }\r\n                let link = route(\"members.store\");\r\n                let meth = \"POST\";\r\n                if(this.edit == true){\r\n                    link = route(\"members.update\", this.editId);\r\n                    formData.append(\"_method\", \"PATCH\");\r\n                }\r\n                axios({\r\n                    method: meth,\r\n                    url: link,\r\n                    data: formData,\r\n                })\r\n                    .then((response) => {\r\n                        _this.disableSubmit = false;\r\n                        _this.message = response.data[\"message\"];\r\n                        setTimeout(function () {\r\n                            _this.message = null;\r\n                        }, 3000);\r\n                        console.log(response);\r\n                        let ele = {'name' : response.data.data['name'], 'code' : response.data.data['id']} ;\r\n                        _this.$emit('message', response.data[\"message\"]);\r\n                        if(_this.edit == true){\r\n                            _this.$emit('venue', [ele, 'update']);\r\n                        }else{\r\n                            _this.$emit('venue', [ele, 'add']);\r\n                        }\r\n                        _this.$emit('all', response.data.all);\r\n                        _this.$emit('close');\r\n                        _this.undoEdit();\r\n                    })\r\n                    .catch((error) => {\r\n                        console.log(error);\r\n                        _this.errorsSubmit = error.response.data?.errors;\r\n                        _this.disableSubmit = false;\r\n                        if (error.response.data[\"message\"]) {\r\n                            _this.message = error.response.data[\"message\"];\r\n                            setTimeout(function () {\r\n                                _this.message = null;\r\n                            }, 3000);\r\n                        }\r\n                    });\r\n            },\r\n            // selectVModel(selectedOption, id){\r\n            //     this.editId = selectedOption.id;\r\n            //     this.selectedForm.heading  = 'Edit ' + selectedOption.name;\r\n            //     this.selectedForm.buttonText  = 'Update'; \r\n            //     this.editBtn = 'Add New Venue'\r\n            //     this.venueDetails.name = selectedOption.name;\r\n            //     this.venueDetails.address = selectedOption.address;\r\n            // },\r\n            undoEdit(){\r\n                this.editId = null;\r\n                this.selectedForm.heading  = 'Add Your Venue';\r\n                this.selectedForm.buttonText  = 'Submit'; \r\n                this.venueDetails.name = null;\r\n                this.venueDetails.address = null;\r\n                this.edit = false;\r\n                this.selectedEdit = false;\r\n            }\r\n        }\r\n    }\r\n</script>\r\n<style src=\"vue-multiselect/dist/vue-multiselect.min.css\"></style>\r\n\r\n<style scoped>\r\n.testi-popup-cont{\r\n    width: 100%;\r\n    height: 100%;\r\n    position: fixed;\r\n    background: rgba(0, 0, 0, 0.50);\r\n    z-index: 999999999999999;\r\n    top: 0;\r\n    left: 0;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n.testi-form-cont{\r\n    border-radius: 8.388px;\r\n    background: #FFF;\r\n    box-shadow: 0px 3.35501px 50.3252px 0px rgba(0, 0, 0, 0.25);\r\n    width: 100%;\r\n    padding: 30px;\r\n    max-width: 620px;\r\n}\r\n.testi-form-head {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n}\r\n.testi-form-head  > div {\r\n    color: #000;\r\n    font-family: Poppins;\r\n    font-size: 20px;\r\n    font-style: normal;\r\n    font-weight: 600;\r\n    line-height: normal;\r\n    display: flex;\r\n    flex-direction: column;\r\n}\r\n.testi-form-head  > div > span:nth-child(2){\r\n    margin-top: 5px;\r\n    display: block;\r\n    width: 45px;\r\n    height: 6px;\r\n    background: #C4456F;\r\n}\r\n.testi-form-que{\r\n    color: #000;\r\n    font-family: Poppins;\r\n    font-size: 18px;\r\n    font-style: normal;\r\n    font-weight: 400;\r\n    line-height: normal;\r\n}\r\n.testi-form-que > span{\r\n    font-weight: 600;\r\n}\r\n.testi-form-cont textarea, .testi-form-cont input {\r\n    border-radius: 7px;\r\n    border: 0.8px solid #A4A4A4;\r\n    padding: 10px;\r\n    background-color: white !important;\r\n    width: 100%;\r\n    color: #898989;\r\n    font-family: Poppins;\r\n    font-size: 15px;\r\n    font-style: normal;\r\n    font-weight: 400;\r\n    line-height: 165%;\r\n}\r\n/* .testi-form-cont input{\r\n    padding: 10px;\r\n} */\r\n.testi-form-cont textarea:hover{\r\n    background-color: white !important;\r\n}\r\n.testi-form-cont > form  button{\r\n    border:none;\r\n    outline: none;\r\n    margin-bottom: 10px;\r\n    border-radius: 11px;\r\n    background: #461952;\r\n    padding: 18px 33px;\r\n    color: #FFF;\r\n    font-family: Mulish;\r\n    font-size: 12.581px;\r\n    font-style: normal;\r\n    font-weight: 800;\r\n    line-height: normal;\r\n    display: inline-block;\r\n    margin-left: auto;\r\n}\r\n</style>"],"sourceRoot":""}]);
